:root {
  --ng-ui-error: #B05468;
}

/********************************************************************************** ELEMENTS **********************************************************************************/

*, *:before, *:after {
  box-sizing: border-box;
}

*[hidden] {
  display: none !important;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  overflow: hidden;
}

.content-scroll {
  overflow: auto;
}

.content-scroll::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}

.content-scroll::-webkit-scrollbar {
  width: 8px;
  background-color: #F5F5F5;
}

.content-scroll::-webkit-scrollbar-thumb {
  background-color: #2B9BA6;
}

html {
  font-size: 14px;
  font-family: "Open Sans", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  min-height: 100%;
}

html,
body {
  display: flex;
  flex-direction: column;
  flex: 1;
  background-color: #efefef;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-columns {
  display: flex;
  flex-direction: column;
}

.flex-aic {
  align-items: center;
}

.flex-jcc {
  justify-content: center;
}

.flex-grow {
  flex-grow: 1;
}

.mr1 {
  margin-right: 1rem;
}

.mt1 {
  margin-top: 1rem;
}

.mt2 {
  margin-top: 2rem;
}

.font-size10 {
  font-size: 1rem;
}

.font-size12 {
  font-size: 1.2rem;
}

.font-size15 {
  font-size: 1.5rem;
}

.font-size20 {
  font-size: 2rem;
}

.uppercase {
  text-transform: uppercase;
}

.underline {
  text-decoration: underline;
}

.white {
  color: white;
}

h1 {
  font-size: 1.6rem;
  color: #0077c1;
  border-bottom: solid 1px;
}

h1 i {
  margin-right: 1rem;
}

h1:first-of-type {
  margin-top: 0;
}

input[type=text],
input[type=number],
input[type=tel],
input[type=password],
input[type=email],
input[type=search],
select,
textarea {
  font-size: 1rem;
  border: 1px solid var(--ng-ui-border-color);
  border-radius: 5px;
  height: 29px;
  padding: 10px;
  box-shadow: 0px 0px 3px 0 #CFCFCF;
}

ng-ui-form-controls, .ng-ui-form-control {
  max-width: 100% !important;
}

.box {
  padding: 5px 10px;
  border: solid 1px #4e4e4e;
  border-bottom: none;
  border-left: solid 3px #0077c1;
}

.box.error {
  border-left: solid 3px red;
  color: red;
}

.box:last-of-type {
  border-bottom: solid 1px #4e4e4e;
}

.red {
  color: red;
}

.green {
  color: green;
}

.grey {
  color: lightgray;
}

.pointer {
  cursor: pointer;
}

.text-center {
  text-align: center;
}